* {margin: 0; padding: 0;
  box-sizing: border-box;}

header {
  background-color: black;
  border-radius: 30px 30px 0 0;
  padding: 2rem;
  text-align: center;
  width: 60%;
  margin: auto;
  color: white;
  font-size: 1.5rem;
}

.list {
  padding: 20px 10px;
  border-bottom: 1px solid lightgray;
  border-top: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
  width: 60%;
  margin: auto;
}

.list:nth-of-type(even){
  background-color: black;
  color: white;
}

.listname {
  width: 90%;
  font-weight: bold;
}

.good {
  width: 10%;
  text-align: right;
}

.good>span {
  cursor: pointer;
}

.date {
  width: 100%;
  font-size: 0.75rem;
  padding-top: 10px;
}

.weather {
  text-align: right;
  width: 100%;
}

.del, .push{
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid lightgray;
  background-color: transparent;
  color: lightgray;
  margin: 1rem 0;
}

.del:hover, .push:hover{
  background-color: black;
  color: white;
}

.input {
  text-align: right;
  margin: 10px auto;
  width: 60%;
  white-space: nowrap;
}

.ip, .ip2{
  padding: 3px;
  border: 1px solid lightgray;
  border-radius: 5px;
  outline: none;
  margin-left: 1rem;
}

.ip2{
  margin-top: 0.5rem;
}

.ip:focus, .ip2:focus{
  border: 1px solid black;
}

.modalbtn {
  background-color: black;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 10px;
}

.modal{
  padding: 1rem 0;
}